@import url('https://fonts.googleapis.com/css?family=Cantarell');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#containerNoMargin {
  margin-left: 0;
  margin-right: 0;
  margin-top: 100px;
}

.SaturnVSVG {
  width: 6em;
  height: auto;
  float: left;
  transition: transform .2s;
  position: relative;
  z-index: 100;
}

.SaturnVSVG:hover {
  transform: scale(1.12);
}

.trails-main {
  animation: fadeout;
  animation-delay: 1.5s;
  animation-duration: 0.6s;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
  z-index: 2000;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
}

.trails-text {
  position: relative;
  width: 100%;
  height: 80px;
  line-height: 80px;
  color: palevioletred;
  font-family: Cantarell, Arial, Helvetica, sans-serif;
  font-weight: 800;
  font-size: 6em;
  text-transform: uppercase;
  will-change: transform, opacity;
  overflow: hidden;
}

.trails-text > div {
  overflow: hidden;
}

.overflow-hidden {
  overflow: hidden;
}

@keyframes fadeout {
  0% {
    transform: translate3d(0, 0px, 0);
    opacity: 1;
    height: 100vh;
  }
  99%{
    transform: translate3d(0, -40px, 0);
    opacity : 0;
    height : 100vh;
  }
  100%{
    transform: translate3d(0, -40px, 0);
    opacity : 0;
    height : 0;
  }
}

.header {
  font-family: Cantarell, Arial, Helvetica, sans-serif;
  font-weight: 700;
  font-style: italic;
  font-size: 4em;
}

.saturn-v-container {
  display: flex;
  flex-direction: column;
}

.stage-enter {
  opacity: 0;
  transform: translateX(-2%);
}
.stage-enter-active {
  opacity: 1;
  transform: translateX(0%);
}
.stage-exit {
  opacity: 1;
  transform: translateX(0%);
}
.stage-exit-active {
  opacity: 0;
  transform: translateX(15%);
}
.stage-enter-active,
.stage-exit-active {
  transition: opacity 500ms, transform 500ms;
}

.saturn-info-container {
  background-color: rgb(24, 27, 36);
  border-radius: 1em;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.full-area {
  width: 100%;
  height: 100%;
}

.info-content-wrapper {
  margin: 1em;
}

.zoomed {
  transform: scale(1.15);
  position: relative;
  z-index: 1000;
}

.grayscale {
  filter: brightness(50%);
  
}
